import React, { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { auth, db } from '../../firebase-config';
import { doc, getDoc } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';
import * as d3 from 'd3';
import './Dashboard.css';

function Dashboard() {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(true);
  
  const forecastChartRef = useRef(null);
  
  // Check authentication state
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
        
        // Fetch user data from Firestore
        try {
          const userDocRef = doc(db, "users", currentUser.uid);
          const userDocSnap = await getDoc(userDocRef);
          
          if (userDocSnap.exists()) {
            setUserData(userDocSnap.data());
          } else {
            console.warn("No user data found");
          }
        } catch (error) {
          console.error("Error fetching user data:", error);
        }
      } else {
        // User not logged in, redirect to sign in page
        navigate('/signin');
      }
      setLoading(false);
    });
    
    return () => unsubscribe(); // Cleanup subscription
  }, [navigate]);
  
  // Create the forecast chart using D3
  useEffect(() => {
    if (!forecastChartRef.current || loading) return;
    
    createForecastChart();
    
    // Handle window resize
    const handleResize = () => {
      // Clear and redraw the chart
      if (forecastChartRef.current) {
        forecastChartRef.current.innerHTML = '';
        createForecastChart();
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [loading, forecastChartRef.current]);
  
  const createForecastChart = () => {
    const margin = { top: 20, right: 20, bottom: 30, left: 40 };
    const width = forecastChartRef.current.clientWidth - margin.left - margin.right;
    const height = 200 - margin.top - margin.bottom;
    
    const svg = d3.select(forecastChartRef.current)
      .append("svg")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", `translate(${margin.left}, ${margin.top})`);
      
    // Data for forecast (hours and energy levels)
    const hours = ["8AM", "10AM", "12PM", "2PM", "4PM", "6PM", "8PM"];
    const energyData = [
      { hour: "8AM", value: 90, actual: true },
      { hour: "10AM", value: 78, actual: true },
      { hour: "12PM", value: 62, actual: false },
      { hour: "2PM", value: 75, actual: false },
      { hour: "4PM", value: 68, actual: false },
      { hour: "6PM", value: 55, actual: false },
      { hour: "8PM", value: 40, actual: false }
    ];
    
    // X axis scale
    const x = d3.scaleBand()
      .range([0, width])
      .domain(hours)
      .padding(0.2);
      
    svg.append("g")
      .attr("transform", `translate(0, ${height})`)
      .call(d3.axisBottom(x));
      
    // Y axis scale
    const y = d3.scaleLinear()
      .domain([0, 100])
      .range([height, 0]);
      
    svg.append("g")
      .call(d3.axisLeft(y));
      
    // Add line path for forecast
    const line = d3.line()
      .x(d => x(d.hour) + x.bandwidth() / 2)
      .y(d => y(d.value))
      .curve(d3.curveMonotoneX);
      
    svg.append("path")
      .datum(energyData)
      .attr("fill", "none")
      .attr("stroke", "#4299e1")
      .attr("stroke-width", 3)
      .attr("d", line);
      
    // Add dots
    svg.selectAll(".dot")
      .data(energyData)
      .enter()
      .append("circle")
      .attr("class", "dot")
      .attr("cx", d => x(d.hour) + x.bandwidth() / 2)
      .attr("cy", d => y(d.value))
      .attr("r", 5)
      .attr("fill", d => d.actual ? "#4299e1" : "#E2E8F0")
      .attr("stroke", "#4299e1")
      .attr("stroke-width", 2);
      
    // Add the current time indicator
    const currentTime = new Date().getHours();
    let timeIndex = Math.floor((currentTime - 8) / 2);
    if (timeIndex >= 0 && timeIndex < hours.length - 1) {
      const pos1 = x(hours[timeIndex]) + x.bandwidth() / 2;
      const pos2 = x(hours[timeIndex + 1]) + x.bandwidth() / 2;
      const posX = pos1 + ((pos2 - pos1) * ((currentTime % 2) / 2));
      
      svg.append("line")
        .attr("x1", posX)
        .attr("x2", posX)
        .attr("y1", 0)
        .attr("y2", height)
        .attr("stroke", "#FC8181")
        .attr("stroke-width", 2)
        .attr("stroke-dasharray", "5,5");
        
      svg.append("text")
        .attr("x", posX)
        .attr("y", 10)
        .attr("text-anchor", "middle")
        .style("font-size", "10px")
        .style("fill", "#FC8181")
        .text("Now");
    }
  };
  
  // Handle button click for tracking meal
  const handleTrackMeal = () => {
    // Placeholder for future implementation
    alert("Track meal functionality will be implemented soon.");
  };
  
  // Show loading state
  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading your dashboard...</p>
      </div>
    );
  }
  
  return (
    <div className="dashboard-container">
      <div className="header">
        <h1>Energy Management Dashboard</h1>
        <div className="date" id="current-date">
          {new Date().toLocaleDateString('en-US', { 
            weekday: 'long', 
            year: 'numeric', 
            month: 'long', 
            day: 'numeric' 
          })}
        </div>
      </div>
      
      <div className="dashboard">
        <div className="card energy-gauge-container">
          <h3 className="card-title">
            Daily Energy Forecast
            <span className="info-icon" title="Predicts how your energy levels will change throughout the day based on planned activities and historical patterns."></span>
          </h3>
          <div id="forecast-chart" ref={forecastChartRef} className="forecast-chart"></div>
        </div>

        <div className="card status-summary-container">
          <h3 className="card-title">
            Quick Actions
            <span className="info-icon" title="One-tap access to common activities for logging your day and managing your condition."></span>
          </h3>
          <div className="quick-actions">
            <button className="action-button meal" onClick={handleTrackMeal}>Track Meal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
