import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { getFirestore, doc, setDoc } from 'firebase/firestore';
import { Loader2, Check, AlertCircle } from 'lucide-react';

export default function FitbitCallback() {
  const [status, setStatus] = useState('processing'); // 'processing', 'success', 'error'
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const processOAuthCallback = async () => {
      try {
        // Get the code from URL query parameters
        const urlParams = new URLSearchParams(location.search);
        const code = urlParams.get('code');

        if (!code) {
          throw new Error('Authorization code not found in the callback URL');
        }

        // In a real application, you would exchange this code for an access token
        // by calling your backend API which would securely store the token
        
        // For demo purposes, we'll simulate a successful token exchange
        console.log('Received authorization code:', code);
        
        // Get user ID - in a real app this would be from your auth context
        const userId = localStorage.getItem('userId') || 'current_user_id';
        
        // Store a record of successful connection in Firestore
        const db = getFirestore();
        await setDoc(doc(db, 'users', userId, 'connected_services', 'fitbit'), {
          connected: true,
          connectedAt: new Date().toISOString(),
          // In a real app, you'd store metadata but NOT tokens here (those should be on your secure backend)
          lastSyncDate: null
        });

        // Simulate API call delay
        await new Promise(resolve => setTimeout(resolve, 2000));
        
        setStatus('success');
      } catch (error) {
        console.error('Error processing Fitbit callback:', error);
        setError(error.message);
        setStatus('error');
      }
    };

    processOAuthCallback();
  }, [location]);

  const handleContinue = () => {
    // Navigate to the appropriate next page after successful connection
    navigate('/dashboard');
  };

  const handleRetry = () => {
    // Navigate back to the Fitbit connect page
    navigate('/fitbit-connect');
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-blue-600 text-white p-4 shadow-md">
        <div className="container mx-auto">
          <h1 className="text-2xl font-bold">Energy Balance</h1>
          <p className="text-sm">Completing Fitbit Connection</p>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto flex-grow p-4 md:p-6 flex items-center justify-center">
        <div className="bg-white rounded-lg shadow-lg p-6 max-w-lg mx-auto text-center">
          {status === 'processing' && (
            <>
              <Loader2 size={48} className="mx-auto mb-4 text-blue-500 animate-spin" />
              <h2 className="text-xl font-semibold mb-2">Processing Your Fitbit Connection</h2>
              <p className="text-gray-600">
                Just a moment while we complete your Fitbit account connection...
              </p>
            </>
          )}

          {status === 'success' && (
            <>
              <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Check size={32} className="text-green-500" />
              </div>
              <h2 className="text-xl font-semibold mb-2 text-green-600">Connection Successful!</h2>
              <p className="text-gray-600 mb-6">
                Your Fitbit account has been successfully connected to Energy Balance.
                You can now track your energy expenditure and activity data.
              </p>
              <button 
                onClick={handleContinue}
                className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
              >
                Continue to Dashboard
              </button>
            </>
          )}

          {status === 'error' && (
            <>
              <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <AlertCircle size={32} className="text-red-500" />
              </div>
              <h2 className="text-xl font-semibold mb-2 text-red-600">Connection Failed</h2>
              <p className="text-gray-600 mb-2">
                We encountered a problem while connecting your Fitbit account:
              </p>
              <p className="text-red-500 font-medium mb-6">
                {error || 'Unable to complete the connection process.'}
              </p>
              <button 
                onClick={handleRetry}
                className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
              >
                Try Again
              </button>
            </>
          )}
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-gray-100 py-4 border-t mt-auto">
        <div className="container mx-auto text-center text-gray-600 text-sm">
          <p>Energy Balance Device Setup |  2025 Energy Balance Inc.</p>
        </div>
      </footer>
    </div>
  );
}