import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './ConnectDevicePage.css';

export default function ConnectDevicePage() {
  const [selectedDevice, setSelectedDevice] = useState(null);
  const navigate = useNavigate();

  const devices = [
    {
      id: 'fitbit',
      name: 'Fitbit',
      description: 'Connect your Fitbit device to track activity and health metrics',
      icon: ''
    },
    {
      id: 'apple-watch',
      name: 'Apple Watch',
      description: 'Connect your Apple Watch to track activity and health metrics',
      icon: ''
    },
    {
      id: 'garmin',
      name: 'Garmin',
      description: 'Connect your Garmin device to track activity and health metrics',
      icon: ''
    }
  ];

  const handleDeviceSelect = (deviceId) => {
    setSelectedDevice(deviceId);
    if (deviceId === 'fitbit') {
      navigate('/fitbit-connect');
    } else {
      // For other devices, show a message that they're not yet supported
      alert('This device type is not yet supported. Please choose Fitbit for now.');
    }
  };

  return (
    <div className="connect-device-container">
      <div className="connect-device-content">
        <h1>Connect Your Device</h1>
        <p className="description">
          Choose your wearable device to start tracking your energy levels and activity.
        </p>
        
        <div className="device-grid">
          {devices.map((device) => (
            <div
              key={device.id}
              className={`device-card ${selectedDevice === device.id ? 'selected' : ''}`}
              onClick={() => handleDeviceSelect(device.id)}
            >
              <div className="device-icon">{device.icon}</div>
              <h3>{device.name}</h3>
              <p>{device.description}</p>
            </div>
          ))}
        </div>

        <div className="navigation-buttons">
          <button 
            className="back-button"
            onClick={() => navigate('/')}
          >
            Back
          </button>
          <button 
            className="continue-button"
            disabled={!selectedDevice}
            onClick={() => handleDeviceSelect(selectedDevice)}
          >
            Continue
          </button>
        </div>
      </div>
    </div>
  );
}