import React, { useState, useEffect } from 'react';
import './App.css';
import { auth, db } from '../firebase-config';
import { getFirestore, addDoc, collection, query, limit, getDocs, where } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';
import { Routes, Route, Navigate, useNavigate, useLocation } from 'react-router-dom';
import ConnectDevicePage from './ConnectDevice/ConnectDevicePage';
import FitbitCallback from './FitbitCallback';
import FitbitConnect from './FitbitConnect';
import SignInPage from './SignIn/SignInPage';
import Dashboard from './Dashboard/Dashboard';
import LandingPage from './LandingPage';

// Protected Route component to handle authentication
function ProtectedRoute({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false);
      
      // If not authenticated and trying to access protected route, redirect to signin
      if (!currentUser && location.pathname !== '/signin') {
        navigate('/signin', { replace: true });
      }
    });

    return () => unsubscribe();
  }, [navigate, location]);

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading...</p>
      </div>
    );
  }

  return user ? children : null;
}

// Public route that redirects to dashboard if user is already logged in
function PublicRoute({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false);
      
      // If authenticated and trying to access public route like signin, redirect to dashboard
      if (currentUser) {
        navigate('/dashboard', { replace: true });
      }
    });

    return () => unsubscribe();
  }, [navigate]);

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading...</p>
      </div>
    );
  }

  return !user ? children : null;
}

function App() {
  return (
    <Routes>
      <Route path="/" element={<LandingPage />} />
      <Route path="/signin" element={
        <PublicRoute>
          <SignInPage />
        </PublicRoute>
      } />
      <Route path="/connect-device" element={
        <ProtectedRoute>
          <ConnectDevicePage />
        </ProtectedRoute>
      } />
      <Route path="/fitbit-callback" element={<FitbitCallback />} />
      <Route path="/fitbit-connect" element={
        <ProtectedRoute>
          <FitbitConnect />
        </ProtectedRoute>
      } />
      <Route path="/dashboard" element={
        <ProtectedRoute>
          <Dashboard />
        </ProtectedRoute>
      } />
    </Routes>
  );
}

export default App;